body {
  color: #fff;
  background-color: #020b12;
  margin: 0;
  padding: 0;
  font-family: Zen Dots, cursive;
}

.video-bg {
  height: 100vh;
  width: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
  overflow: hidden;
}

.video-bg video {
  z-index: 1;
  width: 100%;
  height: 100vh;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
}

.effects {
  object-fit: cover;
  z-index: 2;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, .6);
  position: absolute;
  top: 0;
  left: 0;
}

.video-bg__content {
  z-index: 2;
  color: #fff;
  font-size: 50px;
}

.main {
  max-width: 1600px;
  margin: 50px auto 0;
  padding: 0 20px;
}

